import gsap from "gsap";

@Component({})
export default class PageBanner extends Vue {
  mounted() {
    gsap.from(".toy-1", {
      y: 2000,
      ease: "ease.out",
      duration: 20,
      repeat: 1000000,
    });
    gsap.from(".toy-2", {
      y: 2000,
      ease: "ease.out",
      duration: 20,
      repeat: 1000000,
      delay: 1.5,
    });
    gsap.from(".toy-3", {
      y: 2000,
      ease: "ease.out",
      duration: 20,
      repeat: 1000000,
      delay: 2.5,
    });
    gsap.from(".toy-4", {
      y: 2000,
      ease: "ease.out",
      duration: 20,
      repeat: 1000000,
      delay: 3.5,
    });
    gsap.from(".toy-5", {
      y: 2000,
      ease: "ease.out",
      duration: 20,
      repeat: 1000000,
      delay: 4.5,
    });
  }
}
